import React, { useEffect } from 'react';
import './App.css';
import LogIn from './component/LogIn';
import SignUp from './component/SignUp';
import LogOut from './component/LogOut';
import VacationList from './component/user/VacationList';
import VacationAdmin from './component/admin/VacationAdmin';
import Statistics from './component/admin/Statistics';
import 'bootstrap/dist/css/bootstrap.min.css';

import { Switch, Route, Link, useHistory } from "react-router-dom";

export default function App() {

  let user = JSON.parse(localStorage.getItem('userInfo'));
  const history = useHistory();

  useEffect(() => {
    if (user === null) {
      history.push('/component/LogIn');
    } else if (user !== null && user.isAdmin === true) {
      history.push('/component/VacationAdmin');
    } else {
      history.push('/component/VacationList');
    }
  }, [])

  return (
    <div>
      <nav>
        {/* <ul>
          {(() => {
            if (user !== null && user.isAdmin === true) {
              return <>
                <li><Link to="/component/Statistics">Statistics</Link></li>
                <li style={{ float: 'right' }}><Link to="/component/LogOut">Logout</Link></li>
                <li style={{ float: 'right', marginTop: '15px', color: 'black' }}>hello {user.userName}</li>
              </>
            }
            else if (user !== null && user.isAdmin !== true) {
              return <>
                <li style={{ float: 'right' }}><Link to="/component/LogOut">Logout</Link></li>
              </>
            }

          })()}
        </ul> */}
      </nav>
      <Switch>
        <Route path="/component/LogIn"> <LogIn /> </Route>
        <Route path="/component/SignUp"><SignUp /> </Route>
        <Route path="/component/LogOut"> <LogOut /> </Route>
        <Route path="/component/VacationList"><VacationList /> </Route>
        <Route path="/component/VacationAdmin"><VacationAdmin /> </Route>
        <Route path="/component/Statistics"><Statistics /></Route>
      </Switch>
    </div>
  );
}