import React, { useState, useEffect } from 'react';
import LogOut from '../LogOut';
import Card from 'react-bootstrap/Card';
import Modal from 'react-bootstrap/Modal';
import { Button, Form, Col } from 'react-bootstrap';
import { domain } from '../../config';
import { useHistory,Link } from "react-router-dom";
import Statistics from './Statistics';


export default function VacationAdmin() {
    const history = useHistory();

    if (localStorage.getItem('userInfo') === null || JSON.parse(localStorage.getItem('userInfo')).isAdmin == false) {
        history.push("LogIn");
    }
    const [description, setDescription] = useState('');
    const [location, setLocation] = useState('');
    const [image, setImage] = useState('');
    const [start, setStart] = useState('');
    const [end, setEnd] = useState('');
    const [price, setPrice] = useState('');
    const [vacList, setVacList] = useState([]);
    const [deleteVac, setDeleteVac] = useState(0);
    const [editVac, seteditVaction] = useState(0);
    const [vacId, setVacId] = useState(0);
    const [show, setShow] = useState(false);
    const [whichButton, setWhichButton] = useState('');
    const handleClose = () => setShow(false);
    function handleShow(whichBtn) {
        setShow(true);

        if (whichBtn === 'edit') {
            setDescription(vacId.description);
            setLocation(vacId.location);
            setPrice(vacId.price);
            setImage(vacId.image);
            setStart(vacId.start_at);
            setEnd(vacId.end_at)
        }
    }
    useEffect(() => {
        async function vacationAdmin() {
            const req = await fetch(domain + `/vactionAdmin`);
            const data = await req.json();
            console.log(data);
            setVacList(data);
        }
        vacationAdmin();
    }, [deleteVac, editVac])


    async function dleteVacation(vacId) {
        const req = await fetch('http://localhost:3001/deleteVacation', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ vacId })
        });
        const data = await req.text();
        setDeleteVac(new Date())
        console.log(data);
    }


    async function addOrEditVaction() {
        const req = await fetch(domain + `/${whichButton}`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                description: description,
                location: location,
                image: image,
                start_at: start,
                end_at: end,
                price: price,
                vacId
            })
        });

        const data = await req.json();
        console.log(data);
        setShow(false)
        seteditVaction(new Date())
    }



    /////////////////
    return (
        <div >
            <nav>
                <ul>
                    <li>
                    <button onClick={() => { handleShow(); setWhichButton('addVacation') }}>add vacation</button>
                    </li>
                    <li><Link to="/component/Statistics">Statistics</Link></li>
                    <li><LogOut/></li>
                </ul>
            </nav>
            <h1 style={{ textAlign: 'center', marginTop: '0%' }}>hellow admin</h1>
            { vacList.map(item => {
                return (
                    // <Card className='Card' style={{ float: "left", width: '17rem', backgroundColor: 'white', marginLeft: '2px', borderColor: 'lightblue' }}>
                    //     <Image key={item.image} src={item.image} fluid style={{ width: '17rem', height: '180px' }} />

                    //     <closeButton style={{ color: 'gray', float: 'right', marginRight: '5px' }}
                    //         onClick={() => dleteVacation(item.vac_id)}>‚ùå</closeButton><br /><br />

                    //     <editButton className="btn btn" onClick={() => { handleShow('edit'); setVacId(item); setWhichButton('editVacation') }}
                    //         style={{ color: 'gray', float: 'right', marginRight: '5px' }}>üñã</editButton>

                    //     {item.followers_mount}
                    // location: {item.location}
                    //     <p>description: {item.description}</p>
                    //     <p style={{ textAlign: 'left' }}>${item.price}</p>
                    //     <p>start: {item.start_at} end: {item.end_at} </p>

                    //     <p style={{ textAlign: 'left' }}>{item.vac_id}</p>
                    // </Card>



                    <div style={{ float: 'left', marginLeft: '5px' }}>
                        <Card className='Card' style={{ width: '15rem' }}>
                            <Button size='sm' variant=''
                            //   style={{ color: 'gray', float: 'right', marginRight: '5px' }}
                                onClick={() => dleteVacation(item.vac_id)}>‚ùå</Button>
                            <Card.Img variant="top" key={item.image} src={item.image} />

                                 <Button size='sm' variant='' onClick={() => { handleShow('edit'); setVacId(item); setWhichButton('editVacation') }}
                            style={{ color: 'gray', float: 'right', marginRight: '5px' }}>üñã</Button>

                            <Card.Text>
                                <p style={{ float: 'right' }}>followers: {item.followers_mount}</p>
                        location: {item.location}<br />
                        description: {item.description}<br />
                        ${item.price}<br />
                        start: {item.start_at}<br />
                         end: {item.end_at}<br />
                                <p style={{ textAlign: 'left' }}>{item.vac_id}</p>
                            </Card.Text>
                        </Card>
                    </div>

                )
            })
            }


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                </Modal.Header>
                <Modal.Body>
                    <Form style={{ marginLeft: '2%', marginRight: '2%' }} >
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom01">
                                <Form.Label>Description</Form.Label>
                                <Form.Control
                                    // onBeforeInputCapture={setDescription(vacId.description)}

                                    onChange={(e) => { setDescription(e.target.value) }}
                                    required
                                    type="text"
                                    placeholder="Description"
                                    defaultValue={vacId.description}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>Price</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setPrice(vacId.price)}
                                    onChange={(e) => setPrice(e.target.value)}
                                    type="number"
                                    placeholder="Price"
                                    required
                                    defaultValue={vacId.price}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom02">
                                <Form.Label>Location</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setLocation(vacId.location)}
                                    onChange={(e) => setLocation(e.target.value)}
                                    required
                                    type="text"
                                    placeholder="Location"
                                    defaultValue={vacId.location}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustomUsername">
                                <Form.Label>Image</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setImage(vacId.image)}
                                    onChange={(e) => setImage(e.target.value)}
                                    type="text"
                                    placeholder="Image"
                                    aria-describedby="inputGroupPrepend"
                                    required
                                    defaultValue={vacId.image}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>Start</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setStart(vacId.start_at)}
                                    onChange={(e) => setStart(e.target.value)}
                                    type="date"
                                    placeholder="Start"
                                    required
                                    defaultValue={vacId.start_at}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>End</Form.Label>
                                <Form.Control
                                    // onBeforeInput={setEnd(vacId.end_at)}
                                    onChange={(e) => setEnd(e.target.value)}
                                    type="date"
                                    placeholder="End"
                                    required
                                    defaultValue={vacId.end_at}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Button type='button' onClick={() => addOrEditVaction(whichButton)}>send</Button>
                    </Form>
                </Modal.Body>
            </Modal>
        </div >
    )
}