import React from 'react';
import { useHistory } from "react-router-dom";

export default function LogOut() {
    
    const history = useHistory();

    function logOut() {

        if (history === undefined) {
            return;
        }
        localStorage.clear();
        history.push("./LogIn");
    }
    return (
        <div>
            <button onClick={logOut} style={{float:'right'}}>log out</button>
        </div>
    )
}