import React, { useState, useEffect } from 'react';
import Card from 'react-bootstrap/Card';
import { useHistory } from "react-router-dom";
import { domain } from '../../config';
import { Button } from 'react-bootstrap';
import LogOut from '../LogOut'
export default function VactionList() {
    const history = useHistory();
    let user = 'a';
    if (localStorage.getItem('userInfo') === null || JSON.parse(localStorage.getItem('userInfo')).isAdmin === true) {
        history.push('LogIn')
    }
    else {
        user = JSON.parse(localStorage.getItem('userInfo'));
    }
    const [vacList, setVacList] = useState([]);
    const [isfollowe, setIsfollwe] = useState('');
    const [search, setSearch] = useState('');
    const [date, setDate] = useState('');
    

    useEffect(() => {
        async function vacations() {
            const req = await fetch(domain + `/vaction/${user.userId}`);
            const data = await req.json();
            setVacList(data[0]);
            console.log(vacList);
        }
        vacations();
    }, [isfollowe])


    async function followHandler(vacation) {
        try {
            await fetch(domain + `/isFollow`, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ user_id: user.userId, vacation })
            });
            setIsfollwe(new Date());
        }
        catch (err) {
            console.log(err);
        }
    }


    async function serachHandel() {
        try {
            const sendReq = await fetch(domain + '/searchVacation', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ search, date, user: user.userId })
            });
            const data = await sendReq.json();
            setVacList(data[0]);
        }
        catch (err) {
            console.log(err);
        }
    }



    function clearHandel() {
        setIsfollwe(new Date());
        setSearch('');
    }


    return (
        <div>
            <div style={{textAlign:'center'}}>
            <LogOut/>
            <h1>hello {user.userName}</h1>
            
            </div>
            
            <div style={{ marginLeft: '30%', marginRight: '30%' }}>

                <input type='text' value={search} placeholder='search by description' onChange={(e) => { setSearch(e.target.value) }} />
                <input type="date" onChange={(e) => { setDate(e.target.value) }} />
                <Button variant="outline-primary" size='sm' onClick={serachHandel}>Search</Button>
                <Button variant="outline-primary" size='sm' onClick={clearHandel}>clear</Button>
            </div>
            {/* <SerachVacation setVacList={setVacList} /> */}
            <br />
            {vacList.map(item => {
                return (
                    <div style={{ float: 'left', marginLeft: '5px' }}>
                        <Card className='Card' style={{ width: '15rem' }}>
                            <Card.Img variant="top" key={item.image} src={item.image} />
                            <Card.Text>
                                <span style={{ float: 'right' }}>
                                    <input id={item.vac_id} type="checkbox" checked={item.follower_id != null} onChange={() => followHandler(item.vac_id)} style={{ float: 'right', visibility: 'hidden' }} />
                                    {(() => {
                                        if (item.follower_id != null) {
                                            return <label htmlFor={item.vac_id} style={{ float: 'right' }}>‚ù§Ô∏è</label>
                                        }
                                        else {
                                            return <label htmlFor={item.vac_id} style={{ float: 'right' }}>ü§ç</label>
                                        }
                                    })()}
                                    <p style={{ float: 'right' }}>{item.followers_mount}</p>
                                </span>
                            location: {item.location}<br />
                            description: {item.description}<br />
                            ${item.price}<br />
                            start: {item.start_at}<br />
                             end: {item.end_at}<br />
                                <p style={{ textAlign: 'left' }}>{item.vac_id}</p>
                            </Card.Text>
                        </Card>
                    </div>
                )
            })
            }
        </div >
    )
}