import React, { useState } from 'react'
import { Button, Form, Col } from 'react-bootstrap';
import { useHistory } from "react-router-dom";

import { domain } from '../config';

export default function SignUp() {
    const history = useHistory();

    const [first_name, setfirst_name] = useState('');
    const [last_name, setlast_name] = useState('');
    const [user_name, setuser_name] = useState('');
    const [password, setPassword] = useState('');

    async function signUpHandler() {
        if (first_name === '' || last_name === '' || user_name === '' || password === '') {
            alert('all fildes are mandatori');
            return;
        }
        const sendReq = await fetch(domain + '/signUp', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                first_name: first_name,
                last_name: last_name,
                user_name: user_name,
                password: password
            })
        });
        const data = await sendReq.json()
        if (data === 'user name allready exzist') {
            return alert(data);
        }
        history.push("LogIn");
    }
    return (
        <div>
            <Form style={{ marginLeft: '2%' }} >
                <Form.Row>
                    <Form.Group as={Col} md="4" controlId="validationCustom01">
                        <Form.Label>First name</Form.Label>
                        <Form.Control
                            onChange={(e) => setfirst_name(e.target.value)}
                            required
                            type="text"
                            placeholder="First name"
                        />
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} md="4" controlId="validationCustom02">
                        <Form.Label>Last name</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            placeholder="Last name"
                            onChange={(e) => setlast_name(e.target.value)}
                        />
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} md="4" controlId="validationCustomuser_name">
                        <Form.Label>user_name</Form.Label>
                        <Form.Control
                            type="text"
                            placeholder="user_name"
                            aria-describedby="inputGroupPrepend"
                            required
                            onChange={(e) => setuser_name(e.target.value)}
                        />
                    </Form.Group>
                </Form.Row>

                <Form.Row>
                    <Form.Group as={Col} md="4" controlId="validationCustom03">
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                            type="password"
                            placeholder="Password"
                            required
                            onChange={(e) => setPassword(e.target.value)} />
                    </Form.Group>
                </Form.Row>
                <Button type='button' onClick={signUpHandler}>SignUp</Button>
            </Form>
        </div>
    )
}



















